* {
  font-family: "Raleway", serif;
  font-optical-sizing: auto;
  font-weight: 400;
  font-style: normal;
} 

.text-justify {
  text-align: justify;
}

.bg-nav {
    background-color: #ffffff;
}

.clickable {
    cursor: pointer;
}

.btn-lg {
    background-color: #0a822e;
    border: none;
}

.btn-lg:hover {
    background-color: #1eb84c;
}
.btn-lg:active {
    background-color: #086123;
}

.bg-main {
    background: linear-gradient(to bottom right, #0a822e, #064e1c);
    color: #ffffff;
}

.bg-dark-grey {
  background-color: rgb(31, 31, 31);
}

.caption {
  color: #0a822e;
}

  .img-service {
    opacity: 1;
    transition: ease-in-out 0.3s;
  }
  /* Mostra il testo al passaggio del mouse */
  .img-service:hover  {
    opacity: 1;
  }
  
  /* Aggiunge un velo scuro sopra l'immagine quando si fa hover */
  .img-service {
    position: relative;
    overflow: hidden;
    width: 100%;
    object-fit: cover;
  }
  
  .square {
    aspect-ratio: 1/1;
  }

  .img-service::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.4); /* Inizialmente trasparente */
    transition: background 0.3s ease-in-out;
  }
  
  .img-service:hover::before {
    background: rgba(0, 0, 0, 0.8); /* Oscura leggermente l'immagine */
  }

  a .service {
    color: white;
  }